body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  text-align: center;
/*  vertical-align: middle;*/
}

.main-target {
  width: 100%;
  height: 100%;
  vertical-align: middle;
  text-align: center;

  justify-content: center;
}

.load-photo {
	display: flex;
	border: 1px dashed #3F8AE0;
	border-radius: 10px;
	height: 140px;
	margin-left: 12px;
	margin-right: 12px;
	margin-top: 4px;
	color: #3F8AE0;
}

.load-text {
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center; 
}

.text {
	margin:0;
}

.form {
	text-align: left;
	margin-left: 12px;
	margin-right: 12px;
}

.form-text {
	margin-top: 26px;

	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 18px;
	letter-spacing: -0.154px;
	color: #6D7885;
}

.form-input {
	background: #F2F3F5;
	border: 0.5px solid rgba(0, 0, 0, 0.12);
	box-sizing: border-box;
	border-radius: 10px;
	width: 100%;
	padding: 12px;
	font-size: 16px;
	color: #818C99;
}

.form-button {
	background: #4986CC;
	border-radius: 10px;
	color: #FFFFFF;
	height: 44px;
	width: 100%;
	border: none;
	font-size: 17px;
}

.Place__footer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  height: 75px;
  padding: 12px 12px;
  background-color: #fff;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  text-align: center;
/*  vertical-align: middle;*/
}

.main-more {
	margin-left: 12px;
	margin-right: 12px;
	text-align: left;
}

.more-radio {
	width: 24px;
	height: 24px;
	background: #B8C1CC;
}

.form-radio {
	display: flex;
  	align-items: center; 
  	height: 44px;
}

.more-radio {
	width: 24px;
	height: 24px;
	background: #B8C1CC;
}

.more-radio input[type=radio]:checked{
	width: 24px;
	height: 24px;
	background: #3F8AE0;
}

.label-more {
	display: inline-block;
	cursor: pointer;
	position: relative;
	padding-left: 25px;
	margin-right: 0;
	line-height: 18px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.form-button-disabled {
	background: #b6cfeb;
	border-radius: 10px;
	color: #FFFFFF;
	height: 44px;
	width: 100%;
	border: none;
	font-size: 17px;
}
.Footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  z-index: 1;
  left: 0;
  bottom: 0;
  right: 0;
  height: 75px;
  border-top: 1px solid #919191;
  padding: 0 8px;
  background-color: #fff;
}

.Footer__orders {
  display: inline-block;
  width: 250px;
  border-radius: 15px;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  background-color: #4bb34b;
  color: #fff;
}

